import _ from 'lodash';
import classNames from 'class-names';
import React, { Component } from 'react';
import './App.css';
import ReactHighcharts from 'react-highcharts';
import Slider from 'rc-slider';
import Icon from 'react-oui-icons';

import constants from './constants';
import fns from './fns';

import 'rc-slider/assets/index.css';

class DistributionChart extends Component {
  getDistributionData = () => {
    const {
      distributions,
      position,
      sampleRate,
      variations,
    } = this.props;

    const series = [];
    
    Array(variations).fill(1).forEach((value, index) => {
      series.push({
        name: !!index ? `Variation ${index}` : 'Original',
        data: distributions[index].slice(0, parseInt(position / sampleRate)),
        animation: {
          duration: 0,
        },
      })
    });

    return _.cloneDeep(series);
  }

  render() {
    const {
      policy,
      sampleRate,
      distributions,
    } = this.props;

    const chartRef = React.createRef();

    const config = {
      credits: {
        enabled: false,
      },
      plotOptions: {
        line: {
          marker: {
            enabled: false,
          },
        },
      },
      series: this.getDistributionData(),
      title: {
        text: constants.CHART_LABELS[policy],
      },
      xAxis: {
        labels: {
          formatter: function() {
            return this.value * sampleRate;
          }
        },
        min: 0,
        max: parseInt(distributions[0].length),
      },
      yAxis: {
        title: {
          text: 'Distribution',
        },
      },
    }

    return (
      <div className="flex--1">
        <ReactHighcharts
          config={ config }
          ref={ chartRef }
        />
      </div>
    );
  }
}

class VariationCard extends Component {
  render() {
    const {
      conversionCount,
      title,
      visitorCount,
    } = this.props;

    return (
      <div
        className="flex flex--column flex-align--center soft--sides">
        <h5>
          { title }
        </h5>
        <p className="micro muted">
          Conversions
        </p>
        <h3>
          { conversionCount }
        </h3>
        <p className="micro muted">
          Visitors
        </p>
        <h3>
          { visitorCount }
        </h3>
        <p className="micro muted">
          Conversion Rate
        </p>
        <h3>
          { `${visitorCount ? ((conversionCount / visitorCount) * 100).toFixed(2) : '0.00'}%` }
        </h3>
      </div>
    )
  }
}

class StatSigCard extends Component {
  formatPValue = () => {
    const { pValue } = this.props;

    return ((1 - pValue) * 100).toFixed(2);
  }

  render() {
    const {
      color,
      pValue,
      title,
    } = this.props;

    return (
      <div className="flex flex--column flex-align--center soft--sides width--50">
        <h5>
          { title }
        </h5>
        <h3 className={ classNames({
          [color]: pValue <= 0.10
        }) }>
          { this.formatPValue() }
        </h3>
      </div>
    );
  }
}

class SummaryCard extends Component {
  getVisitorCount = (variation) => {
    const {
      counts,
      position,
    } = this.props;

    return counts[position].visitors[variation];
  }

  getConversionCount = (variation) => {
    const {
      counts,
      position,
    } = this.props;

    return counts[position].conversions[variation];
  }

  getPValue = (variation) => {
    const {
      confidences,
      position,
    } = this.props;

    return confidences[position].pValues[variation];
  }

  isBetterThanBaseline = (variation) => {
    const baselineConversionRate = this.getConversionCount(0) / this.getVisitorCount(0);
    const variationConversionRate = this.getConversionCount(variation) / this.getVisitorCount(variation);
    return Number.isNaN(baselineConversionRate) || Number.isNaN(variationConversionRate) ? false : variationConversionRate > baselineConversionRate;
  };

  renderVariationCards = () => {
    const { variations } = this.props;

    let conversionTotal = 0;
    let visitorTotal = 0;

    return Array(variations + 1).fill(1).map((value, index) => {
      if (index < variations) {
        conversionTotal += this.getConversionCount(index);
        visitorTotal += this.getVisitorCount(index);
      }
      if (!index) {
        return (
          <VariationCard
            conversionCount={ this.getConversionCount(0) }
            key={ index }
            title="Original"
            visitorCount={ this.getVisitorCount(0) }
          />
        );
      } else if (index === variations) {
        return (
          <VariationCard
            conversionCount={ conversionTotal }
            key={ index }
            title="Total"
            visitorCount={ visitorTotal }
          />
        );
      } else {
        return (
          <VariationCard 
            conversionCount={ this.getConversionCount(index) }
            key={ index }
            title={ `Variation ${index}` }
            visitorCount={ this.getVisitorCount(index) }
          />
        );
      }
    });
  };

  renderStatSigCards = () => {
    const { variations } = this.props;

    return Array(variations - 1).fill(1).map((value, index) => {
      return (
        <StatSigCard
          color={ this.isBetterThanBaseline(index + 1) ? 'green' : 'red' }
          key={ index }
          pValue={ this.getPValue(index) }
          title={ `Variation ${index + 1}` }
        />
      )
    });
  };

  render() {
    const { variations } = this.props;
    return (
      <div
        className="border--left flex flex-align--center flex--column"
        style={ {
          minWidth: `${125 * variations}px`,
        } }
      >
        <div className="flex flex--row flex--space width--100">
          { this.renderVariationCards() }
        </div>
        <h4 className="flex">
          Statistical Significance:
        </h4>
        <div className="flex flex--row flex--space width--100">
          { this.renderStatSigCards() }
        </div>
      </div>
    )
  }
}

class App extends Component {
  constructor(props) {
    super(props);

    this.sampleRate = window.uniform.length / 500;
    this.minRegretDistributions = fns.getDistributions(window.minRegret, this.sampleRate);
    this.minTimeDistributions = fns.getDistributions(window.minTime, this.sampleRate);
    this.uniformDistributions = fns.getDistributions(window.uniform, this.sampleRate);
    this.minRegretCounts = fns.getCounts(window.minRegret);
    this.minTimeCounts = fns.getCounts(window.minTime);
    this.uniformCounts = fns.getCounts(window.uniform);
    this.minRegretConfidences = fns.getConfidenceNumbers(window.minRegret);
    this.minTimeConfidences = fns.getConfidenceNumbers(window.minTime);
    this.uniformConfidences = fns.getConfidenceNumbers(window.uniform);
    this.maxVisitors = Math.max(window.minRegret.length, window.minTime.length, window.uniform.length);
    this.minRegretVariations = window.minRegret[0].trafficAllocation.length;
    this.minTimeVariations = window.minTime[0].trafficAllocation.length;
    this.uniformVariations = window.uniform[0].trafficAllocation.length;
  }

  state = {
    currentPosition: 0,
    enabledCharts: [constants.MANUAL],
    interval: null,
    isRunning: false,
    multiplier: 100,
  };

  componentDidMount() {
    document.addEventListener('keydown', this.handleKeyDown);
  }

  handleKeyDown = (event) => {
    switch( event.keyCode ) {
      case 32:
        event.preventDefault();
        if (this.state.isRunning) {
          this.stopCounting();
        } else {
          this.startCounting();
        }
        break;
      default: 
        break;
    }
  };

  isChartEnabled = (chartType) => {
    return this.state.enabledCharts.includes(chartType);
  };

  setCurrentPosition = (position) => {
    this.setState(() => {
      return {
        currentPosition: position
      }
    });
  };

  startCounting = () => {
    if (this.state.currentPosition >= this.maxVisitors) {
      this.setState(() => {
        return {
          currentPosition: 0,
        };
      }, this.startInterval);
    } else {
      this.startInterval();
    }
    this.setState(() => {
      return {
        isRunning: true,
      };
    });
  };

  startInterval = () => {
    this.setState(() => {
      return {
        interval: setInterval(() => {
          if (this.state.currentPosition >= this.maxVisitors) {
            this.stopCounting();
            return;
          }
          this.setState((_previousState) => {
            return {
              currentPosition: _previousState.currentPosition + _previousState.multiplier >= this.maxVisitors ? this.maxVisitors : _previousState.currentPosition + _previousState.multiplier,
            };
          });
        }, 100),
      }
    });
  };

  stopCounting = () => {
    clearInterval(this.state.interval);
    this.setState(() => {
      return {
        interval: null,
        isRunning: false,
      };
    });
  };

  toggleChart = (event) => {
    const chartType = event.target.value;

    if (this.state.enabledCharts.includes(chartType)) {
      const charts = _.cloneDeep(this.state.enabledCharts);
      this.setState(() => {
        return {
          enabledCharts: charts.filter(type => type !== chartType),
        };
      });
    } else {
      this.setState((previousState) => {
        const charts = _.cloneDeep(previousState.enabledCharts)
        charts.push(chartType);
        return {
          enabledCharts: charts,
        };
      });
    }
  };

  render() {
    const marks = {
      0: {
        style: {
          color: 'grey',
        },
        label: 'Start',
      },
      20000: {
        style: {
          color: 'grey',
        },
        label: 'End'
      }
    }

    if (this.isChartEnabled(constants.MANUAL)) {
      marks['7900'] = {
        style: {
          color: 'red',
        },
        label: 'A/B Significant (Loser)',
      };
    }

    if (this.isChartEnabled(constants.MIN_TIME)) {
      marks['13200'] = {
        style: {
          color: 'green',
        } ,
        label: 'Min Time Significant (Winner)',
      };

      marks['4650'] = {
        style: {
          color: 'red',
        },
        label: 'Min Time Significant (Loser)',
      };
    };

    return (
      <div className="soft--sides">
        <div className="push--ends flex flex--row flex-align--center width--100">
          <div className="push-double--right flex flex--column">
            <div className="flex--row micro">
              <input
                checked={ this.isChartEnabled(constants.MANUAL) }
                id={ constants.MANUAL }
                onChange={ this.toggleChart }
                type="checkbox"
                value={ constants.MANUAL }
              />
              <label htmlFor={ constants.MANUAL }>
                A/B
              </label>
            </div>
            <div className="flex--row micro">
              <input
                checked={ this.isChartEnabled(constants.MIN_REGRET) }
                id={ constants.MIN_REGRET }
                onChange={ this.toggleChart }
                type="checkbox"
                value={ constants.MIN_REGRET }
              />
              <label htmlFor={ constants.MIN_REGRET }>
                Min Regret
              </label>
            </div>
            <div className="flex--row micro">
              <input
                checked={ this.isChartEnabled(constants.MIN_TIME) }
                id={ constants.MIN_TIME }
                onChange={ this.toggleChart }
                type="checkbox"
                value={ constants.MIN_TIME }
              />
              <label htmlFor={ constants.MIN_TIME }>
                Min Time
              </label>
            </div>
          </div>
          <div className="push-double--right">
            { !this.state.interval && (
              <button
                className="no-padding"
                onClick={ this.startCounting }>
                <div className="flex square--50 flex-align--center flex-justify--center">
                  { this.state.currentPosition !== this.maxVisitors && (
                    <Icon
                      name="play"
                      size="large"
                    />
                  ) }
                  { this.state.currentPosition === this.maxVisitors && (
                    <Icon
                      name="refresh"
                      size="large"
                    />
                  ) }
                </div>
              </button>
            ) }
            { this.state.interval && (
              <button
                className="no-padding"
                onClick={ this.stopCounting }>
                <div className="flex square--50 flex-align--center flex-justify--center">
                  <Icon
                    name="pause"
                    size="large"
                  />
                </div>
              </button>
            ) }
          </div>
          <div className="flex flex--1 flex-align--center push-double--right">
            <Slider
              dots={ false }
              min={ 0 }
              marks={ marks }
              max={ this.maxVisitors }
              onChange={ this.setCurrentPosition }
              value={ this.state.currentPosition }
            />
          </div>
        </div>
        { this.isChartEnabled(constants.MANUAL) && (
          <div className="flex flex--row border push--ends">
            <DistributionChart
              distributions={ this.uniformDistributions }
              policy={ constants.MANUAL }
              position={ this.state.currentPosition }
              sampleRate={ this.sampleRate }
              variations={ this.uniformVariations }
            />
            <SummaryCard
              confidences={ this.uniformConfidences }
              counts={ this.uniformCounts }
              position={ this.state.currentPosition }
              variations={ this.uniformVariations }
            />
          </div>
        ) }
        { this.isChartEnabled(constants.MIN_REGRET) && (
          <div className="flex flex--row border push--ends">
            <DistributionChart
              distributions={ this.minRegretDistributions }
              policy={ constants.MIN_REGRET }
              position={ this.state.currentPosition }
              sampleRate={ this.sampleRate }
              variations={ this.minRegretVariations }
            />
            <SummaryCard
              confidences={ this.minRegretConfidences }
              counts={ this.minRegretCounts }
              position={ this.state.currentPosition }
              variations={ this.minRegretVariations }
            />
          </div>
        ) }
        { this.isChartEnabled(constants.MIN_TIME) && (
          <div className="flex flex--row border push--ends">
            <DistributionChart
              distributions={ this.minTimeDistributions }
              policy={ constants.MIN_TIME }
              position={ this.state.currentPosition }
              sampleRate={ this.sampleRate }
              variations={ this.minTimeVariations }
            />
            <SummaryCard
              confidences={ this.minTimeConfidences }
              counts={ this.minTimeCounts }
              position={ this.state.currentPosition }
              variations={ this.minTimeVariations }
            />
          </div>
        ) }
      </div>
    );
  }
}

export default App;
